<script>
import BasePdfHeader from "@/components/forms/BasePdfHeader";
import { mapGetters } from "vuex";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  name: "DownloadableBuildingForm",
  computed: {
    ...mapGetters([
      "printProperty",
      "buildingApplication",
      "buildingBasicInformation",
      "buildingDetails",
      "buildingOtherDetails",
    ]),
  },
  components: {
    BasePdfHeader,
  },
  watch: {
    printProperty: {
      deep: true,
      handler(status) {
        if (status) {
          this.generateReport();
          this.$store.commit("setPrintProperty", false);
        }
      },
    },
  },
  methods: {
    formatCurrency(str) {
      var parts = str.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      if (parts.length < 2) {
        parts.push("00");
      }
      return parts.join(".");
    },
    generateReport() {
      const buildingId = this.buildingBasicInformation.reference_number;
      const doc = new jsPDF("p", "mm", "a4");
      /** WITH CSS */
      var width = doc.internal.pageSize.getWidth();
      var height = doc.internal.pageSize.getHeight();
      var canvasElement = document.createElement("canvas");
      html2canvas(this.$refs.content, {
        canvas: canvasElement,
        width: 794,
        height: 1124,
      }).then(function(canvas) {
        const img = canvas.toDataURL("image/jpeg", 1);
        doc.addImage(img, "JPEG", 0, 0, width, height);
        doc.save(`building-application-${buildingId}`);
      });
    },
  },
};
</script>

